/* eslint-disable import/no-anonymous-default-export */
import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";
import { useAuth } from "../../contexts/AuthContext";

export default () => {
	const [click, setClick] = useState(false);
	const handleClick = () => setClick(!click);
	const closeMobileMenu = () => setClick(false);
	const handleLogout = () => {
		logout();
		setClick(false);
	};
	const { logout, currentUser } = useAuth();

	return (
		<>
			<nav className="navbar1">
				<Link to="./" className="navbar-logo">
					Chá do Murillo
				</Link>
				<div className="menu-icon" onClick={handleClick}>
					<i className={click ? "fas fa-times" : "fas fa-bars"}></i>
					<h2>Menu</h2>
				</div>
				<ul className={click ? "nav-menu active" : "nav-menu"}>
					<li className="nav-item">
						<Link to="./" className="nav-links" onClick={closeMobileMenu}>
							Home
						</Link>
					</li>
					<li className="nav-item">
						<Link to="./nome" className="nav-links" onClick={closeMobileMenu}>
							Nome
						</Link>
					</li>
					<li className="nav-item">
						<Link to="./tema" className="nav-links" onClick={closeMobileMenu}>
							Tema
						</Link>
					</li>
					<li className="nav-item">
						<Link
							to="./lista-presentes"
							className="nav-links"
							onClick={closeMobileMenu}
						>
							Lista de Presentes
						</Link>
					</li>
					<li className="nav-item">
						<Link
							to="./recados"
							className="nav-links"
							onClick={closeMobileMenu}
						>
							Recados
						</Link>
					</li>
					{/* <li className="nav-item">
						<Link to="./teste" className="nav-links" onClick={closeMobileMenu}>
							Teste
						</Link>
					</li> */}
					{!currentUser && (
						<>
							<li className="nav-item">
								<Link
									to="./login"
									className="nav-links"
									onClick={closeMobileMenu}
								>
									Login
								</Link>
							</li>
						</>
					)}
					{currentUser && (
						<>
							<li className="nav-item">
								<Link to="./home" className="nav-links" onClick={handleLogout}>
									Logout
								</Link>
							</li>
						</>
					)}
				</ul>
			</nav>
		</>
	);
};
