/* eslint-disable import/no-anonymous-default-export */
import React from "react";
// import ComFilhos from './components/ComFilhos';
// import ComParametro from './components/ComParametro';
// import Card from './components/layout/Card';
// import Header from "./components/Layout/Header";
import ListaPresentes from "./components/Pages/ListaPresentes";
// import Teste from "./components/Pages/Teste";
import Home from "./components/Pages/Home";
import Tema from "./components/Pages/Tema";
import Nome from "./components/Pages/Nome";
import Navbar from "./components/Navbar/Navbar";
import Pagamento from "./components/Pages/Pagamento";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import Recados from "./components/Pages/Recados";
import Teste from "./components/Pages/Teste";
import { AuthProvider } from "./contexts/AuthContext";
import Login from "./components/Pages/Login/Login";
import Signup from "./components/Pages/Login/Signup";
import ForgotPassword from "./components/Pages/Login/ForgotPassword";
// import UserProvider from "./UserContext";

export default (props) => (
	<div className="App">
		<Router>
			<AuthProvider>
				<Navbar />
				<Switch>
					<Route path="/tema" component={Tema} />
					<Route path="/nome" component={Nome} />
					<Route path="/pagamento" component={Pagamento} />
					<Route path="/lista-presentes" component={ListaPresentes} />
					<Route path="/recados" component={Recados} />
					<Route path="/login" component={Login} />
					<Route path="/signup" component={Signup} />
					<Route path="/forgot-password" component={ForgotPassword} />
					<Route path="/login" component={Login} />

					<Route path="/teste" component={Teste} />

					<Route path="/ChaBebe-Site/teste" component={Teste} />
					<Route path="/ChaBebe-Site/tema" component={Tema} />
					<Route path="/ChaBebe-Site/nome" component={Nome} />
					<Route path="/ChaBebe-Site/pagamento" component={Pagamento} />
					<Route
						path="/ChaBebe-Site/lista-presentes"
						component={ListaPresentes}
					/>
					<Route path="/ChaBebe-Site/recados" component={Recados} />
					<Route path="/ChaBebe-Site" component={Home} />
					<Route path="/ChaBebe-Site/" component={Home} />
					<Route path="/" component={Home} />
				</Switch>
			</AuthProvider>
		</Router>
	</div>
);
