import React, { useState } from "react";
import "../css/Recado.css";

import firebase from "firebase/app";

import { firestore } from "../../firebase/firebase";
import { useCollectionData } from "react-firebase-hooks/firestore";

function Recado(props) {
	const menssageRef = firestore.collection("messages");
	const query = menssageRef.orderBy("createdAt", "desc").limit(25);

	const [messages] = useCollectionData(query, { idField: "id" });
	const [formName, setFormName] = useState(
		props.user ? props.user?.displayName : ""
	);
	const [formText, setFormText] = useState("");
	let msgUID = "";
	const addRecado = async (e) => {
		e.preventDefault();
		await menssageRef.add({
			name: formName,
			text: formText,
			createdAt: firebase.firestore.FieldValue.serverTimestamp(),
		});
		setFormName(props.user ? props.user?.displayName : "");
		setFormText("");
	};

	const deleteMsg = async (msgUID) => {
		await menssageRef
			.doc(msgUID)
			.delete()
			.then(() => {
				console.log("Post Deletado!");
			});
		console.log(msgUID);
	};
	return (
		<div className="comment-box">
			<h2>Mural de Recados</h2>
			<div className="img-center">
				<img
					alt=""
					className="raposa-recado"
					src="https://static.vecteezy.com/ti/vetor-gratis/p1/672409-mamae-raposa-vermelha-e-bebe-lendo-um-livro-vetor.jpg"
				></img>
			</div>
			<form className="comment-form" onSubmit={addRecado}>
				<div className="comment-form-fields">
					<input
						value={formName}
						placeholder="Nome"
						required
						onChange={(e) => setFormName(e.target.value)}
					></input>
					<br />
					<textarea
						placeholder="Mensagem"
						rows="4"
						required
						value={formText}
						onChange={(e) => setFormText(e.target.value)}
					></textarea>
				</div>
				<div className="comment-form-actions">
					<button type="submit">Deixar Recado</button>
				</div>
			</form>
			{messages &&
				messages.map((msg) => (
					<Comment
						key={msg.createdAt}
						msg={msg}
						user={props.user}
						deleteRecado={deleteMsg.bind(msgUID)}
					/>
				))}
		</div>
	);
}

function Comment(props) {
	const userUID = props.user?.uid;
	const msgID = props.msg?.id;
	// function deleteRecado() {
	// 	if (window.confirm("Apagar recado?")) {
	// 		console.log("apagado");
	// 	}
	// 	console.log(props.msg);
	// }
	return (
		<div className="comment">
			<p className="comment-header">{props.msg?.name}</p>
			<p className="comment-body">- {props.msg?.text}</p>
			{userUID === "YbEUkEPcQmgRPOacGd3UVhIT4O93" && (
				<>
					<div className="comment-footer">
						<a
							href="#"
							className="comment-footer-delete"
							onClick={() => props.deleteRecado(msgID)}
						>
							Apagar
						</a>
					</div>
				</>
			)}
		</div>
	);
}

export default Recado;
